<template>
  <div>
    <select
      v-if="side !== 'user'"
      v-model="stage"
      style="background-color: rgb(214, 221, 225); font-weight: bolder; margin-top: 30px"
      @change="doFilter"
      class="form-control"
    >
      <option
        v-for="stage in $store.getters.stages"
        :key="stage.value"
        :value="stage.value"
      >
        {{ stage.text }}
      </option>
    </select>
    <select
      v-if="side !== 'user'"
      v-model="unit"
      style="background-color: rgb(214, 221, 225); font-weight: bolder; margin-top: 30px"
      @change="doFilter"
      class="form-control"
    >
      <option v-for="section in units" :key="section._id" :value="section.number">
        {{ section.name }}
      </option>
      <option :value="null">All</option>
    </select>
    <br />
    <hr />
    <!--------------------------------------------------------------------------->
    <h3
      style="font-weight: bolder; color: #fff; background-color: #537175"
      class="text-center"
    >
      ترتيب الطلبة
    </h3>
    <div v-if="grades.length == 0" class="center">
      <h3 class="text-center">لا يوجد طلبة</h3>
    </div>
    <div style="height: 700px; overflow-y: scroll" class="table-responsive">
      <!--Table-->
      <table class="table">
        <!--Table head-->
        <thead>
          <tr>
            <th class="th-sm">الترتيب</th>
            <th class="th-sm">التقدم المحرز</th>
          </tr>
        </thead>
        <!--Table head-->

        <!--Table body-->
        <tbody style="background-color: rgba(91, 103, 108, 0.17)">
          <tr style="border: 1px solid #444" v-for="(grade, g) in grades" :key="g">
            <td>
              <div class="padge">{{ g + 1 }}</div>
            </td>
            <td style="width: 100%">
              <div class="student text-center">{{ grade.username }}</div>
              <progress-bar
                :degree="grade.degree"
                :fullDegree="fullDegree"
                color="#588c58"
                caption="الامتحانات"
              />
            </td>
          </tr>

          <loading v-if="loading" class="mx-auto" />
        </tbody>
        <!--Table body-->
      </table>
      <!--Table-->
    </div>
  </div>
</template>

<script>
import axios from "axios";
import ProgressBar from "../components/progress-bar.vue";
export default {
  components: {
    ProgressBar,
  },
  props: ["side", "type"],
  created() {
    this.doFilter();
  },
  data() {
    return {
      stage: "6",
      unit: 1,
      degrees: [],
      fullDegree: 0,
      loading: false,
    };
  },
  computed: {
    units() {
      const units = this.$store.getters.sections.filter((sec) => sec.stage == this.stage);
      return units;
    },
    grades() {
      if (this.degrees.length == 0) return [];
      return [...this.degrees].sort((a, b) => {
        return a.degree < b.degree ? 1 : -1;
      });
    },
  },
  methods: {
    async doFilter() {
      this.degrees = [];
      const stage = this.side === "user" ? this.$store.getters.stage : this.stage;
      this.loading = true;
      const res1 = await axios.get(`/getUnitExamsFullDegree/${stage}/${this.unit}`);
      this.fullDegree = res1.data.fullDegree;
      const res2 = await axios.get(`/getStudentsByStageAndUnit/${stage}/${this.unit}`);
      const students = res2.data.students;
      for (let i = 0; i < students.length; i++) {
        const res3 = await axios.get(
          `/getStudentTotalDegree/${stage}/${this.unit}/${students[i]._id}`
        );
        console.log(res3.data);
        this.degrees.push({ degree: res3.data.degree, username: students[i].fullname });
      }
      this.loading = false;
    },
  },
};
</script>

