.table {
  th {
    text-align: center;
  }
  .student {
    font-weight: bolder;
    color: #fff;
    background-color: #2b51639c;
  }
  tr:nth-child(even) {
    background-color: #518a8a5e;
  }
  .padge {
    font-size: 1.7rem;
    font-weight: 900;
    background: rgba(10, 109, 38, 0.782);
    color: #fff;
    border-right: 3px solid #fff;
    text-align: center;
    @media (max-width: 600px) {
      height: 30;
      width: 30;
    }
  }
}
</style>
